<template>
	<div class="am-g am-cf">
		<div class="am-u-sm-12 am-padding-0">

			<div class="am-panel ta-panel">
				<div class="am-panel-hd am-cf">
					<div class="am-input-group am-u-md-5 am-padding-left-0" style="max-width: 350px;">
						<input type="text" class="am-form-field" v-model="queryData" placeholder="例：吃饭">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button" @click="query">
								查询
							</button>
						</span>
					</div>
				</div>
				<div class="am-panel-bd">
					<table class="am-table am-table-striped am-table-bordered am-table-hover ta-table am-margin-0">
						<thead>
							<tr>
								<th>优惠码</th>
								<th>服务项</th>
								<th>消费手机</th>
								<th class="am-hide-sm-only">消费时间</th>
								<th class="am-hide-sm-only">消费商家</th>
								<th style="max-width: 120px;">操作</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(tData, index) in tableData" track-by="index">
								<template v-if="index < rowSelected">
									<td>{{ tData.number }}</td>
									<td>{{ tData.service }}</td>
									<td>{{ tData.mobile }}</td>
									<td class="am-hide-sm-only">{{ tData.time }}</td>
									<td class="am-hide-sm-only">{{ tData.merchant }}</td>
									<td>
										<a href="javascript:;" @click="update(index)">
											<i class="am-icon-edit am-text-lg"></i>
											<span class="am-hide-sm-only">编辑</span>
										</a>
										<a href="javascript:;" class="am-text-danger" @click="remove(index)">
											<i class="am-icon-remove am-text-lg"></i>
											<span class="am-hide-sm-only">删除</span>
										</a>
									</td>
								</template>
							</tr>
						</tbody>
					</table>
				</div>
				<div class="am-panel-footer">
					<div class="am-cf">
						<ul class="am-pagination ta-pagination am-margin-vertical-0 am-pagination-right">
							<li class="ta-sm-hide">
								<a href="javascript:;">共<span class="am-text-danger" v-text="total"></span>条</a>
							</li>
							<li class="ta-sm-hide">
								<a href="javascript:;" v-on:click="home">首页</a>
							</li>
							<li v-if="curr > 1">
								<a href="javascript:;" v-on:click="prev">上一页</a>
							</li>
							<li v-if="curr < pages">
								<a href="javascript:;" v-on:click="next">下一页</a>
							</li>
							<li>
								<a href="javascript:;">共<span class="am-text-danger" v-text="pages"></span>页</a>
							</li>
							<li class="ta-pagination-rowTotal">
								<a class="ta-select-page" href="javascript:;">
									<select v-model="rowSelected" v-cloak>
										<option v-for="row in rowTotal" v-bind:value="row">{{row}}</option>
									</select>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantList',
		data() {
			return {
				merchant: '', //商家数据
				pages: 0, // 总页码
				total: 0, // 总条数
				curr: 1, // 当前页码
				rowTotal: [10, 20, 50, 100], // 每页行数
				rowSelected: 10, // 下拉框 selected 值
				tableData: [], // 数据
				queryData: '', // 查询数据
			}
		},
		created() {
			// 获取数据
			const self = this;
			if(self.$route.query.id){
				self.merchant = self.$route.query.id
			}
			const data = self.getData(21, self.$route.query.id);
			self.tableData = data;
			self.total = data.length;
			self.pages = Math.ceil(self.total / self.rowSelected); //得到总页数
		},
		methods: {
			getData(size, id) {
				const self = this;
				const data = [];
				for(let i = 0; i < size; i++) {
					let randomNumber = $.getRandom(6);
					data.push({
						checked: false,
						number: randomNumber,
						mobile: '186102'+randomNumber,
						service: '开房',
						time: $.getTime(),
						merchant: self.merchant || '商家-'+$.getRandom(1)
					})
				}
				return data;
			},
			next() {
				this.curr += 1;
			},
			prev() {
				this.curr -= 1;
			},
			home() {
				this.curr = 1;
			},
			query() {
				const queryData = this.queryData;
				if(queryData.trim()){
					layer.msg('查询数据：' + queryData)
				}
			},
			update(index) {
				layer.msg('下标：' + index)
			},
			remove(index) {
				var self = this;
				layer.confirm('确定删除?', {
					title: '系统提示',
					btn: ['删除', '取消']
				}, function() {
					layer.closeAll()
					self.tableData.splice(index, 1)
				})

			}
		}
	}
</script>


